<template>
  <div>
    <!-- active는 class속성, text-danger는 v-bind디렉티브 -->
    <span class="active" v-bind:class="errorClass"
      >active,text-danger css 클래스 적용하기</span
    >
  </div>
</template>
<script>
export default {
  data() {
    return {
      errorClass: "text-danger",
    };
  },
};
</script>
<style>
span.active {
  border: solid 1px blue;
}
span.text-danger {
  color: red;
}
</style>
